
import EncounterMixinVue from '@/views/EncounterMixin.vue'
import { FieldType } from "@/components/Forms/BaseFormElements"
import { defineComponent } from 'vue'
import { Field, Option } from '@/components/Forms/FieldInterface'
import { IonPage } from "@ionic/vue"
import { HtsReferralService } from "@/apps/HTS/services/hts_referral_service"
import { HtsCircumcisionService } from '../../services/hts_circumcision_service'
import { Offline } from '@/composables/offline'
import { ObservationOffline } from '@/interfaces/Sync'
import { convertArrayToString } from '@/utils/Strs'

export default defineComponent({
  components: { IonPage },
  mixins: [EncounterMixinVue],
  data: () => ({
    service: {} as any,
    isHivPositive: false as boolean
  }),
  watch: {
    ready: {
      handler(ready: boolean) {
        if (ready) {
          this.service = new HtsReferralService(this.patientID, this.providerID)
          this.fields.push(this.referralServicesField())
          this.fields.push(this.commentsField())
        }
      },
      immediate: true
    }
  },
  methods: {
    onFinish(formData: any, computedData: any) {
      this.saveFormEncounterAndProceed({
        encounterTypeID: this.service.encounterTypeID,
        formData,
        computedData,
      })
    },
    getObs(concept: string) {
      return HtsReferralService.getFirstValueCoded(this.patientID, concept)
    },
    serviceReferral(formData: any, service: string) {
      return formData.services.some((v: Option) => v.value === service)
    },
    referralServicesField(): Field {
      let recommendCircumcision = false
      const knownSTIs: string[] = []
      return {
        id: "services",
        helpText: 'Refer Client to other Services?',
        type: FieldType.TT_MULTIPLE_SELECT,
        isRequired: () => true,
        init: async () => {
          if (this.goOffline()) {
            const offlineData = Offline().findByID(this.offlineID)
            if (offlineData && Array.isArray(offlineData.observationParams)) {
              const offlineObs = (offlineData.observationParams||[])
              // Recommend circumcision if not circumcised
              recommendCircumcision = offlineObs.some((obs: ObservationOffline) => {
                return obs.encounterName === 'circumcision status' && 
                  /no/i.test(`${obs?.meta?.hts_male_circumcised}`) && 
                  this.facts.age > 14 && this.facts.age < 64
              })
              offlineObs.forEach((obs: ObservationOffline) => {
                if (obs.encounterName === 'testing') {
                  (obs?.meta?.test_results||[]).forEach((res: Option) => {
                    if (/syphilis|Hepatitis/i.test(res.label) && res.value === 'Positive')
                      knownSTIs.push(res.label)
                  })
                  this.isHivPositive = /positive/i.test(`${obs?.meta?.hiv_result?.label}`)
                }
              })
              return true
            }
            return false
          }
          // Online mode recommendations
          if (this.facts.gender === 'M') {
            const circumcision = new HtsCircumcisionService(this.patientID, this.providerID)
            const status = (await circumcision.getRecentCircumcisionStatus())
            recommendCircumcision = [
              status === 'No', this.facts.age > 14 && this.facts.age < 64
            ].every(Boolean)
          }
          for(const sti of ["Syphilis Test Result", "Hepatitis B Test Result"]) {
            if ((await this.getObs(sti)) === 'Positive')
              knownSTIs.push(sti)
          }
          this.isHivPositive = ['Positive', 'Positive Initial Professional'].includes((await this.getObs('HIV status')))
          return true
        },
        finalComputedValue: (v: Option[]) => {
          return {
            obs: v.map(d => this.service.buildValueText('Referrals ordered', d.value))
          }
        },
        onValueUpdate(listData: Option[], value: Option) {
          return listData.map(l => {
            if (value.value === 'None' && l.value != 'None') {
              l.isChecked = false
            }
            if (value.value != 'None' && value.isChecked && l.value === 'None') {
              l.isChecked = false
            }
            return l
          })
        },
        options: (f: any) => {
          const options: any = []
          const opt = (v: string, options = {} as any) => {
            const selected = f.services ? f.services.some((d: Option) => d.value === v) : false
            return { label: v, value: v, isChecked: selected, ...options }
          }
          options.push(opt('STI', knownSTIs.length ? {
            description: {
              text: `Client tested positive for ${convertArrayToString(knownSTIs)}. Please recommend this option if client has not started yet`
            }
          } : {}))
          options.push(opt('TB'))
          if (this.facts.gender === 'M') {
            options.push(opt('VMMC', recommendCircumcision ? {
              description: {
                text: 'Client is not circumcised, please recommend this option'
              }
            } : {}))
          }
          options.push(opt('PrEP ', this.isHivPositive ? {
            disabled: this.isHivPositive,
            description: {
              text: 'Client is HIV positive, cannot refer for PrEP'
            }
          } : {}))
          options.push(opt('PEP ', this.isHivPositive ? {
            disabled: this.isHivPositive,
            description: {
              text: 'Client is HIV positive, cannot refer for PEP'
            }
          } : {}))
          options.push(opt('Other'))
          options.push(opt('None'))
          return options
        },
        config: {
          footerBtns: [
            {
              name: "None",
              slot: "end",
              onClickComponentEvents: {
                refreshOptions: (_: any, listData: Option[]) => {
                  return listData.map(o => {
                    o.isChecked = o.label === 'None'
                    return o
                  })
                }
              },
              onClick: () => 'None'
            }
          ]
        }
      }
    },
    commentsField(): Field {
      return {
        id: 'comment',
        helpText: 'Comments (Optional)',
        type: FieldType.TT_NOTE,
        finalComputedValue: (v: Option | null) => {
          if (v && !/^\s*$/gi.test(`${v.value}`)) {
            return {
              obs: this.service.buildValueText('Comments', v.value)
            }
          }
          return null
        }
      }
    }
  }
})
